import React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import usePreviewCopy from '../../common/hooks/usePreviewCopy';

function PreviewHooks({ controller }: { controller: Controller }) {
  usePreviewCopy(controller);
  return null;
}

const createOnEditable = () => {
  return (_, controller: Controller, next: Function) => {
    return (
      <>
        {next()}
        <PreviewHooks controller={controller} />
      </>
    );
  };
};

export default createOnEditable;
